import { Injectable } from '@angular/core';
import { HttpResponse } from '@angular/common/http';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot, Routes } from '@angular/router';
import { UserRouteAccessService } from '../core';
import { Observable, of } from 'rxjs';
import { filter, map } from 'rxjs/operators';
import { GeneralSubjectService } from './general-subject.service';
import { GeneralSubjectManageComponent } from './general-subject-manage.component';
import { GeneralSubjectDetailsComponent } from './general-subject-details.component';
import { GeneralSubjectUpdateComponent } from './general-subject-update.component';
import { IGeneralSubject, GeneralSubject } from '../shared/model/content/general-subject.model';
import { GeneralSubjectCreateComponent } from './general-subject-create.component';
import { GeneralSubjectPublishComponent } from './general-subject-update-publish.component';
import { GeneralSubjectUnpublishComponent } from './general-subject-update-unpublish.component';
import { GeneralSubjectDeleteComponent } from './general-subject-delete.component';

@Injectable({ providedIn: 'root' })
export class GeneralSubjectResolve implements Resolve<IGeneralSubject> {
  constructor(private service: GeneralSubjectService) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<IGeneralSubject> {
    const id = route.params['id'] ? route.params['id'] : null;
    if (id) {
      return this.service.getSubjectBySubjectId(id).pipe(
        filter((response: HttpResponse<GeneralSubject>) => response.ok),
        map((generalSubject: HttpResponse<GeneralSubject>) => generalSubject.body)
      );
    }
    return of(new GeneralSubject());
  }
}

@Injectable({ providedIn: 'root' })
export class GeneralSubjectWithTreeResolve implements Resolve<any> {
  constructor(private service: GeneralSubjectService) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> {
    const id = route.params['id'] ? route.params['id'] : null;
    if (id) {
      return this.service.getSubjectTreeBySubjectId(id).pipe(
        filter((response: HttpResponse<any>) => response.ok),
        map((generalSubject: HttpResponse<any>) => generalSubject.body)
      );
    }
    return of(new GeneralSubject());
  }
}

export const generalSubjectRoute: Routes = [
  {
    path: '',
    component: GeneralSubjectManageComponent,
    data: {
      authorities: ['SYSTEM_ADMIN'],
      pageTitle: 'Manage general subjects'
    },
    canActivate: [UserRouteAccessService]
  },
  {
    path: 'create',
    component: GeneralSubjectCreateComponent,
    data: {
      authorities: ['SYSTEM_ADMIN'],
      pageTitle: 'Create new general subject'
    },
    canActivate: [UserRouteAccessService]
  },
  {
    path: ':id/edit',
    component: GeneralSubjectUpdateComponent,
    resolve: {
      generalSubject: GeneralSubjectWithTreeResolve
    },
    data: {
      authorities: ['SYSTEM_ADMIN'],
      pageTitle: 'Edit general subject',
      action: 'edit'
    },
    canActivate: [UserRouteAccessService]
  },
  {
    path: ':id/view',
    component: GeneralSubjectDetailsComponent,
    resolve: {
      generalSubject: GeneralSubjectWithTreeResolve
    },
    data: {
      authorities: ['SYSTEM_ADMIN'],
      pageTitle: 'View general subject'
    },
    canActivate: [UserRouteAccessService]
  },
  {
    path: ':id/delete',
    component: GeneralSubjectDeleteComponent,
    resolve: {
      generalSubject: GeneralSubjectResolve
    },
    data: {
      authorities: ['SYSTEM_ADMIN'],
      pageTitle: 'Delete general subject'
    },
    canActivate: [UserRouteAccessService]
  },
  {
    path: ':id/publish',
    component: GeneralSubjectPublishComponent,
    resolve: {
      generalSubject: GeneralSubjectResolve
    },
    data: {
      authorities: ['SYSTEM_ADMIN'],
      pageTitle: 'Publish general subject',
      action: 'publish'
    },
    canActivate: [UserRouteAccessService]
  },
  {
    path: ':id/unpublish',
    component: GeneralSubjectUnpublishComponent,
    resolve: {
      generalSubject: GeneralSubjectResolve
    },
    data: {
      authorities: ['SYSTEM_ADMIN'],
      pageTitle: 'Unpublish general subject',
      action: 'unpublish'
    },
    canActivate: [UserRouteAccessService]
  }
];
