import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { INACTIVE } from '../shared/constants/common.constants'
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { PluginService } from '../shared/plugin/plugin.service';
import { GeneralSubjectService } from './general-subject.service';
import { IGeneralSubject } from '../shared/model/content/general-subject.model';
import { CoreErrorHandler } from '../shared/error-handler/error-handler.service';

@Component({
    template: "",
    providers: [GeneralSubjectService,CoreErrorHandler]
})
export class GeneralSubjectUnpublishComponent implements OnInit {
    constructor(
        public router: Router,
        public activatedRoute: ActivatedRoute,
        public generalSubjectService: GeneralSubjectService,
        protected pluginService: PluginService,
        public errorHandler: CoreErrorHandler
    ) { }

    ngOnInit() {
        this.activatedRoute.data.subscribe(({ generalSubject }) => {
            this.unpublishGeneralSubject(generalSubject);
        });
    }

    protected subscribeToSaveResponse(result: Observable<HttpResponse<IGeneralSubject>>) {
        result.subscribe((res: HttpResponse<IGeneralSubject>) => this.onSaveSuccess(res), (errRes: HttpErrorResponse) => this.onError(errRes));
    }

    unpublishGeneralSubject(generalSubject) {
        generalSubject.status = INACTIVE;
        this.subscribeToSaveResponse(this.generalSubjectService.updateGeneralSubjectStatus(generalSubject));
        this.router.navigateByUrl('/general-subject');
    }

    onSaveSuccess(resp: HttpResponse<IGeneralSubject>) {
        if (resp.body.status === INACTIVE) {
            this.pluginService.alertMessage("General subject unpublished successfully", "success")
        }
    }

    onError(errRes: HttpErrorResponse) {
        this.errorHandler.handleError(errRes);
    }
}