import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { RouterModule } from '@angular/router';
import { NgJhipsterModule } from 'ng-jhipster';
import { InfiniteScrollModule } from 'ngx-infinite-scroll';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { SharedModule } from '../shared';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { generalSubjectRoute } from './general-subject.route';
import {
  GeneralSubjectManageComponent,
  GeneralSubjectCreateComponent,
  GeneralSubjectUpdateComponent,
  GeneralSubjectDetailsComponent,
  GeneralSubjectDeleteComponent,
  GeneralSubjectPublishComponent,
  GeneralSubjectUnpublishComponent,
  GeneralSubjectService
} from './index';

@NgModule({
  imports: [
    RouterModule.forChild(generalSubjectRoute),
    FormsModule,
    ReactiveFormsModule,
    SharedModule,
    NgJhipsterModule,
    InfiniteScrollModule,
    NgbModule
  ],
  declarations: [
    GeneralSubjectManageComponent,
    GeneralSubjectCreateComponent,
    GeneralSubjectUpdateComponent,
    GeneralSubjectDetailsComponent,
    GeneralSubjectDeleteComponent,
    GeneralSubjectPublishComponent,
    GeneralSubjectUnpublishComponent
  ],
  providers: [GeneralSubjectService],
  schemas: [CUSTOM_ELEMENTS_SCHEMA]
})

export class GeneralSubjectsModule {
}