import { Component, OnInit, OnDestroy } from "@angular/core";
import { Router } from '@angular/router';
import { JhiEventManager, JhiParseLinks } from 'ng-jhipster';
import { Subscription } from 'rxjs';
import { Principal } from '../core';
import { GeneralSubjectService } from './general-subject.service'
import { IGeneralSubject } from '../shared/model/content/general-subject.model'
import { ITEMS_PER_PAGE } from '../shared';
import { HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { PluginService } from '../shared/plugin/plugin.service';
import { CoreErrorHandler } from '../shared/error-handler/error-handler.service';

declare var $: any;
declare var swal: any;

@Component({
  templateUrl: "./general-subject-manage.component.html",
  providers: [GeneralSubjectService,CoreErrorHandler]
})
export class GeneralSubjectManageComponent implements OnInit, OnDestroy {
  generalSubjects: IGeneralSubject[];
  eventSubscriber: Subscription;
  itemsPerPage: number;
  totalItems: number;
  totalPages: number;
  currentAccount: any;
  predicate: any;
  reverse: any;
  page: any;
  links: any;

  constructor(
    public _router: Router,
    public generalSubjectService: GeneralSubjectService,
    protected eventManager: JhiEventManager,
    protected parseLinks: JhiParseLinks,
    protected principalService: Principal,
    public pluginService: PluginService,
    public errorHandler: CoreErrorHandler
  ) {
    this.generalSubjects = [];
    this.itemsPerPage = ITEMS_PER_PAGE;
    this.page = 0;
    this.links = {
      last: 0
    };
    this.predicate = 'id';
    this.reverse = false;
  }

  reset() {
    this.page = 0;
    this.generalSubjects = [];
    this.loadGeneralSubjects();
  }

  loadPage(page) {
    this.page = page;
    this.loadGeneralSubjects();
  }

  ngOnInit() {
    this.loadGeneralSubjects();
    this.principalService.identity().then(account => {
      this.currentAccount = account;
    });
    this.registerChangeInGeneralSubjects();
  }

  ngOnDestroy() {
    this.eventManager.destroy(this.eventSubscriber);
  }

  trackId(index: number, item: IGeneralSubject) {
    return item.id;
  }

  sort() {
    const result = [this.predicate + ',' + (this.reverse ? 'asc' : 'desc')];
    if (this.predicate !== 'id') {
      result.push('id');
    }
    return result;
  }

  registerChangeInGeneralSubjects() {
    this.eventSubscriber = this.eventManager.subscribe('generalSubjectListModification', response => this.reset());
  }

  loadGeneralSubjects() {
    this.generalSubjectService
      .getAllSubjects({
        page: this.page,
        size: this.itemsPerPage,
        sort: this.sort()
      })
      .subscribe(
        (res: HttpResponse<IGeneralSubject[]>) => this.paginateGeneralSubjects(res.body, res.headers),
        (res: HttpErrorResponse) => this.onError(res)
      );
  }

  protected paginateGeneralSubjects(data: IGeneralSubject[], headers: HttpHeaders) {
    this.links = this.parseLinks.parse(headers.get('link'));
    this.totalItems = parseInt(headers.get('X-Total-Count'), 10);
    if (data.length > 0) {
      for (let i = 0; i < data.length; i++) {
        this.generalSubjects.push(data[i]);
      }
    } else {
      this.pluginService.alertMessage("No data found..!", "warning");
    }
  }

  protected onError(errRes: HttpErrorResponse) {
     this.errorHandler.handleError(errRes);
  }
}





