import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from '@angular/router';
import { IGeneralSubject } from '../shared/model/content/general-subject.model'
import { GeneralSubjectService } from './general-subject.service'
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { PluginService } from '../shared/plugin/plugin.service';
import { CurriculumService } from '../curriculum/curriculum.service';
import { CoreErrorHandler } from '../shared/error-handler/error-handler.service';

declare var $: any;
declare var swal: any;

@Component({
  templateUrl: "./general-subject-details.component.html",
  providers: [GeneralSubjectService,CoreErrorHandler]
})
export class GeneralSubjectDetailsComponent implements OnInit {
  generalSubject: IGeneralSubject[] = [];
  curriculumList: IGeneralSubject[];
  nodeType: string;
  nodeName: string;
  nodeDescription: string;
  subjectId: number;
  curriculum: string;
  grade: string;
  active: boolean;

  constructor(
    public pluginService: PluginService,
    public curriculumService: CurriculumService,
    public generalSubjectService: GeneralSubjectService,
    public activatedRoute: ActivatedRoute,
    public router: Router,
    public errorHandler: CoreErrorHandler
  ) {
    this.active = false;
  }

  ngOnInit() {
    this.pluginService.sideMenuToggle();
    this.activatedRoute.data.subscribe(({ generalSubject }) => {
      this.generalSubject = generalSubject;
      this.updateGeneralSubjectValues(generalSubject);
    });
    this.getCurriculumList();
  }

  updateGeneralSubjectValues(generalSubject: IGeneralSubject[]) {
    this.generalSubject = generalSubject;
    this.pluginService.jsTree("#treeView", this.generalSubject, false);
    this.subjectId = this.generalSubject[0].data.id;
  }

  showConceptAndTopicDetails() {
    var localThisObj = this;
    $('#treeView')
      .on('changed.jstree', function (e, data) {
        var i, j, curriculumId;
        for (i = 0, j = data.selected.length; i < j; i++) {
          localThisObj.nodeType = data.node.type;
          if (localThisObj.nodeType === "root") {
            localThisObj.nodeName = data.node.data.name;
            localThisObj.nodeDescription = data.node.data.description;
            localThisObj.grade = data.node.data.baseGrade;
          }
          else if (localThisObj.nodeType === "concept") {
            localThisObj.nodeName = data.node.data.name;
            localThisObj.nodeDescription = data.node.data.description;
          } else if (localThisObj.nodeType === "topic") {
            localThisObj.nodeName = data.node.data.name;
            localThisObj.nodeDescription = data.node.data.description;
            curriculumId = data.node.data.baseCurriculumId;
            if (curriculumId === null) {
              localThisObj.curriculum = null;
            } else {
              localThisObj.curriculum = localThisObj.curriculumList.find(item => item.id === curriculumId).acronym;
            }
            localThisObj.grade = data.node.data.baseGrade;
          } else {
            localThisObj.nodeType = ""
          }
        }
      })
  }

  ngAfterViewInit() {
    this.showConceptAndTopicDetails();
  }

  toggleTreeNodes() {
    this.active = !this.active;
    if (this.active) {
      this.pluginService.openJsTreeAllNodes("#treeView");
    } else {
      this.pluginService.closeJsTreeAllNodes("#treeView")
    }
  }

  getCurriculumList() {
    this.curriculumService.getStandardCurriculums().subscribe(
      (res: HttpResponse<IGeneralSubject[]>) => this.assignResponseToList(res.body),
      (res: HttpErrorResponse) => this.onError(res)
    )
  }

  assignResponseToList(data: IGeneralSubject[]) {
    this.curriculumList = data;
  }

  onError(errRes: HttpErrorResponse) {
    this.errorHandler.handleError(errRes);
    }

  onBack() {
    var localObj = this;
    swal({
      title: "Are you sure to go previous page ?",
      type: "warning",
      showCancelButton: true,
      confirmButtonText: "Yes",
      confirmButtonColor: "#32b432",
      cancelButtonText: "No",
      closeOnConfirm: true,
      closeOnCancel: true
    },
      function (isConfirm) {
        if (isConfirm) {
          localObj.previousUrl();
          localObj.pluginService.sideMenuToggle();
        }
      });
  }

  previousUrl() {
    this.router.navigateByUrl('/general-subject')
  }
}