export interface IGeneralSubject {
  id?: number;
  title?: string;
  description?: string;
  status?: string;
  data?: {
    orderNumber?: number,
    baseCurriculumId?: number,
    baseGrade?: number,
    description?: string,
    editStatus?: string,
    id?: number,
    name?: string,
    status?: string,
    contentsCount?: number,
    orgContentsCount?: number,
    questionsCount?: number,
    orgQuestionsCount?: number
  };
  icon?: string;
  disabled?: boolean;
  parent?: string;
  text?: string;
  type?: string;
  state?: string;
  a_attr?: string;
  li_attr?: string;
  curriculumID?: number;
  name?: string;
  acronym?: string;
  gradesApplicableFrom?: number,
  gradesApplicableTo?: number,
  grades?: IGeneralSubject[];
}

export class GeneralSubject implements IGeneralSubject {
  constructor(
    public id?: number,
    public title?: string,
    public description?: string,
    public status?: string,
    public data?: {
      orderNumber?: number,
      baseCurriculumId?: number,
      baseGrade?: number,
      description?: string,
      editStatus?: string,
      id?: number,
      name?: string,
      status?: string,
      contentsCount?: number,
      orgContentsCount?: number,
      questionsCount?: number,
      orgQuestionsCount?: number
    },
    public icon?: string,
    public parent?: string,
    public disabled?: boolean,
    public text?: string,
    public type?: string,
    public state?: string,
    public a_attr?: string,
    public li_attr?: string,
    public curriculumID?: number,
    public name?: string,
    public acronym?: string,
    public gradesApplicableFrom?: number,
    public gradesApplicableTo?: number,
    public grades?: IGeneralSubject[]) { }
}
