import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import { createRequestOption } from '../shared';
import { IGeneralSubject } from '../shared/model/content/general-subject.model';
import { ACTIVE } from '../shared/constants/common.constants';
import { generalSubject, generalSubjectSearch, generalSubjectTree } from '../shared/constants/service.constants';

type EntityResponseType = HttpResponse<IGeneralSubject>;
type EntityArrayResponseType = HttpResponse<IGeneralSubject[]>;

let headers: HttpHeaders = new HttpHeaders();
headers = headers.append('Access-Control-Allow-Origin', '*').append('Access-Control-Allow-Headers', '*');

@Injectable({ providedIn: 'root' })
export class GeneralSubjectService {
  public generalSubjectResourceUrl = environment.API_URL + generalSubject;
  public generalSubjectSearchResourceUrl = environment.API_URL + generalSubjectSearch;
  public generalSubjectResourceUrlWithTree = environment.API_URL + generalSubjectTree;

  constructor(protected http: HttpClient) { }

  getSubjectBySubjectId(id: number): Observable<EntityResponseType> {
    return this.http.get<IGeneralSubject>(`${this.generalSubjectResourceUrl}/${id}`, { observe: 'response' });
  }

  getSubjectSearchList(): Observable<EntityArrayResponseType> {
    var reqJSON = {
      "status": ACTIVE
    };
    return this.http.post<IGeneralSubject[]>(this.generalSubjectSearchResourceUrl, reqJSON, { observe: 'response' });
  }

  getSubjectListByGrade(grade, status): Observable<EntityArrayResponseType> {
    var reqJSON = {
      "grade": grade,
      "status": status
    };
    return this.http.post<IGeneralSubject[]>(this.generalSubjectSearchResourceUrl, reqJSON, { observe: 'response' });
  }

  getSubjectTreeBySubjectId(id: number): Observable<EntityArrayResponseType> {
    return this.http.get<IGeneralSubject[]>(`${this.generalSubjectResourceUrlWithTree}/${id}`, { observe: 'response' });
  }

  updateGeneralSubjectStatus(generalSubject: IGeneralSubject): Observable<EntityResponseType> {
    return this.http.put<IGeneralSubject>(this.generalSubjectResourceUrl, generalSubject, { observe: 'response' });
  }

  getAllSubjects(req?: any): Observable<EntityArrayResponseType> {
    const options = createRequestOption(req);
    return this.http.get<IGeneralSubject[]>(this.generalSubjectResourceUrl, { params: options, observe: 'response' });
  }

  delete(id: number): Observable<HttpResponse<any>> {
    return this.http.delete<any>(`${this.generalSubjectResourceUrl}/${id}`, { observe: 'response' });
  }

  saveGeneralSubject(generalSubject: IGeneralSubject[]): Observable<EntityResponseType> {
    return this.http.post<IGeneralSubject>(this.generalSubjectResourceUrlWithTree, generalSubject, { headers: headers, observe: 'response' });
  }
}
