import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { PluginService } from '../shared/plugin/plugin.service';
import { JhiEventManager } from 'ng-jhipster';
import { GeneralSubjectService } from './general-subject.service';
import { CoreErrorHandler } from '../shared/error-handler/error-handler.service';

declare var $: any;
declare var swal: any;

@Component({
    template: "",
    providers: [GeneralSubjectService, CoreErrorHandler]
})
export class GeneralSubjectDeleteComponent implements OnInit {
    constructor(
        public router: Router,
        public activatedRoute: ActivatedRoute,
        protected eventManager: JhiEventManager,
        public generalSubjectService: GeneralSubjectService,
        protected pluginService: PluginService,
        public errorHandler: CoreErrorHandler
    ) { }

    ngOnInit() {
        this.activatedRoute.data.subscribe(({ generalSubject }) => {
            this.deletegeneralSubject(generalSubject);
        });
    }

    protected subscribeToDeleteResponse(resp: Observable<HttpResponse<any>>) {
        resp.subscribe((res: HttpResponse<any>) => this.onDeleteSuccess(res), (res: HttpErrorResponse) => this.onError(res));
    }

    deletegeneralSubject(generalSubject) {
        var localthisObject = this;
        swal({
            title: "Are you sure to delete this general subject ?",
            type: "warning",
            showCancelButton: true,
            confirmButtonText: "Yes",
            confirmButtonColor: "#32b432",
            cancelButtonText: "No",
            closeOnConfirm: false,
            closeOnCancel: true
        },
            function (isConfirm) {
                if (isConfirm) {
                    localthisObject.confirmDelete(generalSubject.id);
                } else {
                    localthisObject.previousUrl();
                }
            });
    }

    confirmDelete(id: number) {
        this.subscribeToDeleteResponse(this.generalSubjectService.delete(id));
    }

    previousUrl() {
        this.router.navigateByUrl('/general-subject');
    }

    protected onDeleteSuccess(resp) {
        this.pluginService.alertMessage("General subject deleted successfully", "success");
        this.previousUrl();
    }

    onError(errRes: HttpErrorResponse) {
        this.errorHandler.handleError(errRes);
    }
}